:root {
  --font-primary: "Lexend Deca", sans-serif;
  --font-secondary: "Big Shoulders Display", cursive;
}

:root {
  --primary-color: hsl(0, 0%, 95%);
  --paragraph-color: hsla(0, 0%, 100%, 0.75);
  --first-card-color: hsl(31, 77%, 52%);
  --second-card-color: hsl(184, 100%, 22%);
  --third-card-color: hsl(179, 100%, 13%);
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  background-color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 5rem 0;
}
@media (min-width: 48em) {
  body {
    padding: 2.5rem;
  }
}
@media (min-width: 64em) {
  body {
    padding: 0;
  }
}

.container {
  width: 90vw;
  max-width: 28.125rem;
  border-radius: 0.5rem;
  overflow: hidden;
}
@media (min-width: 48em) {
  .container {
    display: flex;
    align-items: center;
    justify-content: content;
    align-items: initial;
    max-width: 57.1875rem;
  }
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  font-family: var(--font-secondary);
}

.car {
  animation: movingCar 7s linear;
}

@keyframes movingCar {
  100% {
    transform: translateX(6.875rem);
  }
}
.card {
  padding: 3rem;
}
@media (min-width: 48em) {
  .card {
    padding: 2.25rem;
  }
}
@media (min-width: 64em) {
  .card {
    padding: 3rem;
  }
}
.card:nth-child(1) {
  background-color: var(--first-card-color);
}
.card:nth-child(2) {
  background-color: var(--second-card-color);
}
.card:nth-child(3) {
  background-color: var(--third-card-color);
}
.card__title {
  font-size: 2.5rem;
  text-transform: uppercase;
  color: var(--primary-color);
  margin: 1.75rem 0;
}
.card__description {
  color: var(--paragraph-color);
  letter-spacing: 0.03125rem;
  font-size: 0.9375rem;
  line-height: 1.6;
  opacity: 0.9;
}
@media (min-width: 48em) {
  .card__description {
    line-height: 1.8;
    font-size: 14px;
  }
}
.card__btn {
  display: inline-block;
  background-color: var(--primary-color);
  border-radius: 2rem;
  border: 2px solid var(--primary-color);
  padding: 1rem 1.875rem;
  transition: all ease 0.5s;
  cursor: pointer;
  margin-top: 1.375rem;
  color: #000;
  mix-blend-mode: screen;
}
.card__btn:hover {
  background: none;
  color: var(--primary-color);
}
@media (min-width: 64em) {
  .card__btn {
    padding: 0.75rem 1.625rem;
  }
}
@media (min-width: 64em) {
  .card__btn {
    margin-top: 5rem;
  }
}